import React,{ Component } from 'react';
import { BrowserRouter as Router, Redirect, Route, Link } from "react-router-dom";
import { UserBackgroundForm } from '../components/userbackgroungform/userbackgroundform';
import { Dashboard } from '../components/dashboard/dashboard';


const token = localStorage.getItem('authtoken');

//CHECKING TOKEN IS PRESENT OR NOT
const fakeAuth = {
    isAuthenticated: false,
    authenticate(cb) {
    const token = localStorage.getItem('authtoken');
        if(token){
            this.isAuthenticated = true;
        }else{
            this.isAuthenticated = false;
        }
    },
    signout(cb) {
      this.isAuthenticated = false;
      setTimeout(cb, 100);
    }
};

//AUTHENTICATING ROUTE ON BASES OF TOKEN
function PrivateRoute({ component: Component, ...rest }) {
    fakeAuth.authenticate();
    return (
      <Route
        {...rest}
        render={props =>
          fakeAuth.isAuthenticated ? (
            <Component {...props} />
          ) : (
            <Redirect
              to={{
                pathname: "/"
              }}
            />
          )
        }
      />
    );
}


export class Routing extends React.Component {
    render(){
        return(
            <Router>
            <div className="react-routing">
            <Route exact={true} strict path="/" component ={UserBackgroundForm} />
            <PrivateRoute path="/dashboard" component ={Dashboard} />
            </div>
            </Router>
        )
    }
}