import  axios  from 'axios';
let environment_url = 'http://localhost:2300/';


// SUBMIT PARTICULAR USER INFORMATION
export const submit_details = async (user_info_obj) => {
        // for localhost
        // let res = await axios.post(`${environment_url}submit-info`, user_info_obj)

        // for production mode
        let res = await axios.post('/submit-info', user_info_obj)
        localStorage.setItem('authtoken', res.data.authtoken);
        return res;
}
