import React from 'react';
import { Button, Form, FormGroup, Label, Input, Col, Row } from 'reactstrap';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Header } from '../header/header';
import { submit_details } from '../../providers/api-service/api-service';

export class UserBackgroundForm extends React.Component {

    notify = (msg) => {
        toast.error(msg, {
            position: toast.POSITION.TOP_RIGHT
        });
    }

    constructor(props) {
        super(props);
        this.statelist = [
            "Andhra Pradesh",
            "Arunachal Pradesh",
            "Assam",
            "Bihar",
            "Chhattisgarh",
            "Chandigarh",
            "Dadra and Nagar Haveli",
            "Daman and Diu",
            "Delhi",
            "Goa",
            "Gujarat",
            "Haryana",
            "Himachal Pradesh",
            "Jammu and Kashmir",
            "Jharkhand",
            "Karnataka",
            "Kerala",
            "Madhya Pradesh",
            "Maharashtra",
            "Manipur",
            "Meghalaya",
            "Mizoram",
            "Nagaland",
            "Orissa",
            "Punjab",
            "Pondicherry",
            "Rajasthan",
            "Sikkim",
            "Tamil Nadu",
            "Tripura",
            "Uttar Pradesh",
            "Uttarakhand",
            "West Bengal"
        ];
        this.family_info = []
        this.list_of_family_info = []
        this.state = {
            name: '',
            fathername: '',
            lastname: '',
            occupation: '',
            no_of_family_memeber: '',
            city: '',
            currentState: '',
            area: '',
            address: '',
            contact: '',
            email: '',
            password: '',
            gender: '',
            dob: '',
            caste: '',
            age: '',
            marriedstatus: '',
            isvisible: false,
            err: {
                name_err: '',
                fathername_err: '',
                occupation_err: '',
                no_of_family_memeber_err: '',
                city_err: '',
                area_err: '',
                state_err: '',
                address_err: '',
                contact_err: '',
                age_err: '',
                email_err: '',
                password_err: '',
                dob_err: '',
                gender_err: '',
                lastname_err: '',
                caste_err: ''
            }
        }
        this.handleChange = this.handleChange.bind(this);
    }

    InputHandler(e) {
        const fieldname = e.target.name
        const value = e.target.value
        this.setState({ [fieldname]: value }, () => this.validateform(fieldname, value));

        if (fieldname === 'no_of_family_memeber') {
            if (value >= 1) {
                if (value > 10) {
                    this.notify('It is quite big');
                    this.family_info = [];
                } else {
                    this.setState({ isvisible: true })
                    for (let i = 0; i < value; i++) {
                        this.family_info.push(i);
                    }
                }
            } else {
                this.family_info = [];
                this.setState({ isvisible: false })
            }
        }
    }

    // used to submit data on submit button
    submitinnerform = (e, index) => {
        let youcanpushdata = false;
        let obj = {
            'family_partner_name': this.state['family_partner_name' + index],
            'family_partner_occupation': this.state['family_partner_occupation' + index],
            'family_partner_relation': this.state['family_partner_relation' + index]
        }

        if (this.state['family_partner_name' + index] == undefined || this.state['family_partner_occupation' + index] == undefined
            || this.state['family_partner_relation' + index] == undefined) {
            this.notify('Please fill the details first');
        } else {
            if (this.list_of_family_info.length === 0) {
                this.list_of_family_info.push(obj)
            } else {
                for (let i = 0; i < this.list_of_family_info.length; i++) {
                    if (JSON.stringify(obj) === JSON.stringify(this.list_of_family_info[i])) {
                        youcanpushdata = false;
                        this.notify('You have already filled the same details')
                        break
                    } else {
                        youcanpushdata = true;
                    }
                }
            }
        }
        // update existing one
        if (youcanpushdata === true) {
            if (index <= this.list_of_family_info.length) {
                this.list_of_family_info[index] = obj;
            } else {
                this.list_of_family_info.push(obj);
            }
        }
    }

    validateform(fieldname, value) {
        let name_error = this.state.err.name_err;
        let fathername_error = this.state.err.fathername_err;
        let occupation_error = this.state.err.occupation_err;
        let no_of_family_memeber_error = this.state.err.no_of_family_memeber_err;
        let city_error = this.state.err.city_err;
        let state_error = this.state.err.state_err;
        let address_error = this.state.err.address_err;
        let contact_error = this.state.err.contact_err;
        let email_error = this.state.err.email_err;
        let area_error = this.state.err.area_err;
        let password_error = this.state.err.password_err;
        let age_error = this.state.err.age_err;
        let dob_error = this.state.err.dob_err;
        let gender_error = this.state.err.gender_err;
        let lastname_error = this.state.err.lastname_err;

        var err = { ...this.state.err }

        switch (fieldname) {
            case 'name':
                if (value === '') {
                    name_error = 'Name is required'
                    err.name_err = name_error
                    this.setState({ err })
                } else if ((/^[0-9]/i).test(value) === true) {
                    name_error = 'Please enter a valid entry'
                    err.name_err = name_error
                    this.setState({ err })
                } else if (value.length <= 2) {
                    name_error = 'Please enter a valid name'
                    err.name_err = name_error
                    this.setState({ err })
                } else {
                    err.name_err = ''
                    this.setState({ err })
                }
                break;
            case 'lastname':
                if (value === '') {
                    lastname_error = 'lastname is required'
                    err.lastname_err = lastname_error
                    this.setState({ err })
                } else if ((/^[0-9]/i).test(value) === true) {
                    lastname_error = 'Please enter a valid entry'
                    err.lastname_err = lastname_error
                    this.setState({ err })
                } else if (value.length <= 2) {
                    lastname_error = 'Please enter a valid lastname'
                    err.lastname_err = lastname_error
                    this.setState({ err })
                } else {
                    err.lastname_err = ''
                    this.setState({ err })
                }
                break;
            case 'gender':
                if (value === '') {
                    gender_error = 'Gender is required'
                    err.gender_err = gender_error
                    this.setState({ err })
                } else {
                    err.gender_err = ''
                    this.setState({ err })
                }
                break;
            case 'dob':
                if (value === '') {
                    dob_error = 'DOB is required'
                    err.dob_err = dob_error
                    this.setState({ err })
                } else {
                    err.dob_err = ''
                    this.setState({ err })
                }
                break;
            case 'fathername':
                if (value === '') {
                    fathername_error = 'Father name is required'
                    err.fathername_err = fathername_error
                    this.setState({ err })
                } else {
                    err.fathername_err = ''
                    this.setState({ err })
                }
                break;
            case 'occupation':
                if (value === '') {
                    occupation_error = 'Occupation is required'
                    err.occupation_err = occupation_error
                    this.setState({ err })
                } else {
                    err.occupation_err = ''
                    this.setState({ err })
                }
                break;
            case 'no_of_family_memeber':
                if (value === '') {
                    no_of_family_memeber_error = 'Field is required'
                    err.no_of_family_memeber_err = no_of_family_memeber_error
                    this.setState({ err })
                } else if ((/^[0-9]$/).test(value) === false) {
                    err.no_of_family_memeber_err = 'Please enter a valid number'
                    this.setState({ err })
                } else {
                    err.no_of_family_memeber_err = ''
                    this.setState({ err })
                }
                break;
            case 'city':
                if (value === '') {
                    city_error = 'City is required'
                    err.city_err = city_error
                    this.setState({ err })
                } else if (/\d/.test(value) === true) {
                    city_error = 'Please enter a valid entry'
                    err.city_err = city_error
                    this.setState({ err })
                } else {
                    err.city_err = ''
                    this.setState({ err })
                }
                break;
            case 'state':
                if (value === '') {
                    state_error = 'State is required'
                    err.state_err = state_error
                    this.setState({ err })
                } else {
                    err.state_err = ''
                    this.setState({ err })
                }
                break;
            case 'address':
                if (value === '') {
                    address_error = 'Address is required'
                    err.address_err = address_error
                    this.setState({ err })
                } else {
                    err.address_err = ''
                    this.setState({ err })
                }
                break;
            case 'contact':
                if (value === '') {
                    contact_error = 'Contact is required'
                    err.contact_err = contact_error
                    this.setState({ err })
                } else if (value.length > 10 || value.length < 10) {
                    contact_error = 'please enter a 10 digit number'
                    err.contact_err = contact_error
                    this.setState({ err })
                }
                else if ((/^((\+)?(\d{2}[-]))?(\d{10}){1}?$/).test(value) === false) {
                    err.contact_err = 'Please enter number only'
                    this.setState({ err })
                } else {
                    err.contact_err = ''
                    this.setState({ err })
                }
                break;
            case 'age':
                if (value === '') {
                    age_error = 'Age is required'
                    err.age_err = age_error
                    this.setState({ err })
                } else if (value.length > 3 || value > 110 || value < 5) {
                    err.age_err = 'Please enter a valid entry and minimum 5yr age is required'
                    this.setState({ err })
                } else {
                    err.age_err = ''
                    this.setState({ err })
                }
            case 'area':
                if (value === '') {
                    area_error = 'Current living Area is required'
                    err.area_err = area_error
                    this.setState({ err })
                } else {
                    err.area_err = ''
                    this.setState({ err })
                }
                break;
            case 'email':
                if ((/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i).test(value) === false) {
                    email_error = 'Enter a valid email address'
                    err.email_err = email_error;
                    this.setState({ err })
                } else if (value === '') {
                    email_error = 'Email is required'
                    err.email_err = email_error;
                    this.setState({ err })
                } else {
                    err.email_err = ''
                    this.setState({ err })
                }
                break;
            case 'password':
                if (value.length <= 5) {
                    password_error = 'Password is too short...'
                    err.password_err = password_error
                    this.setState({ err })
                } else if (value === '') {
                    password_error = 'Password is required'
                    err.password_err = password_error
                    this.setState({ err })
                } else {
                    err.password_err = ''
                    this.setState({ err })
                }
                break;
            default:
                break;
        }
    }

    handleChange(event) { this.setState({ currentState: event.target.value }) }

    submitform = async (e) => {
        // remove unnecessary element from array after serveral times change in no_of_family_memeber
        this.list_of_family_info.splice(this.state.no_of_family_memeber, this.list_of_family_info.length);
        // set default state to Andhra pradesh
        if (this.state.currentState === '') {
            let defaultstate = 'Andhra Pradesh'
            this.setState({ currentState: defaultstate })
        }
        // show error msg if any field is having wrong details
        var obj = { ...this.state.err };
        for (let i in obj) {
            if (obj[i] !== '') {
                this.notify('Please fill the correct value in ' + i.split("_", 1));
                break
            }
        }
        console.log('--------------',this.state);
        // check before submission if any field is empty || user miss field to enter his/her details
        if (this.state.name === '' || this.state.fathername === '' || this.state.occupation === '' ||
            this.state.city === '' || this.state.currentState === '' || this.state.area === '' || this.state.address === '' ||
            this.state.contact === '' || this.state.email === '' || this.state.password === '' || this.state.gender === '' ||
            this.state.dob === '' || this.state.age === '' || this.state.lastname === '' || this.state.marriedstatus === '') {
            this.notify('Some fields are not filled above');
        } else if (this.state.no_of_family_memeber == this.list_of_family_info.length) {
            //succesfully filled the form
            const UserBackgroundInfoObject = {
                name: this.state.name,
                lastname: this.state.lastname,
                contact: this.state.contact,
                address: this.state.address,
                caste: this.state.caste,
                city: this.state.city,
                area: this.state.area,
                email: this.state.email,
                password: this.state.password,
                age: this.state.age,
                dob: this.state.dob,
                gender: this.state.gender,
                married: this.state.marriedstatus,
                state: this.state.currentState,
                fathers_name: this.state.fathername,
                occupation: this.state.occupation,
                no_of_family_member: this.state.no_of_family_memeber
            };
            try {
                let res = await submit_details(UserBackgroundInfoObject);
                console.log('res-----------', res);
                if (res.data.status) {
                    toast.success(res.data.msg, {
                        position: toast.POSITION.TOP_RIGHT
                    });
                    this.props.history.push('/dashboard');
                } else {
                    toast.error(res.data.err, {
                        position: toast.POSITION.TOP_RIGHT
                    });
                }
            } catch (error) {
                toast.error(error, {
                    position: toast.POSITION.TOP_RIGHT
                });
            }
        } else {
            toast.error('Please filled the family info above', {
                position: toast.POSITION.TOP_RIGHT
            });
        }
    }



    render() {
        return (
            <div className="register">
                <Header />
                <Form className="backgroundInfo-container" >
                    <FormGroup>
                        <Label for="name" className="label-text" >Firstname</Label>
                        <Input type="text" name="name" maxLength="20" value={this.state.name} placeholder="Enter your name" onChange={(e) => this.InputHandler(e)} />
                        {this.state.err['name_err'] ? <div className="err">{this.state.err['name_err']}</div> : ''}
                    </FormGroup>
                    <FormGroup>
                        <Label for="lname" className="label-text" >Lastname</Label>
                        <Input type="text" name="lastname" maxLength="20" value={this.state.lastname} placeholder="Enter your lastname" onChange={(e) => this.InputHandler(e)} />
                        {this.state.err['lastname_err'] ? <div className="err">{this.state.err['lastname_err']}</div> : ''}
                    </FormGroup>
                    <FormGroup>
                        <Label for="fathername" className="label-text">Fathername</Label>
                        <Input type="text" name="fathername" maxLength="25" value={this.state.fathername} placeholder="Enter your fathername" onChange={(e) => this.InputHandler(e)} />
                        {this.state.err['fathername_err'] ? <div className="err">{this.state.err['fathername_err']}</div> : ''}
                    </FormGroup>
                    <FormGroup>
                        <Label for="caste" className="label-text">Caste/Gotr (Optional)</Label>
                        <Input type="text" name="caste" maxLength="20" value={this.state.caste} placeholder="Enter your caste/gotr" onChange={(e) => this.InputHandler(e)} />
                        {this.state.err['caste_err'] ? <div className="err">{this.state.err['caste_err']}</div> : ''}
                    </FormGroup>
                    <FormGroup>
                        <Label for="occupation" className="label-text">Occupation</Label>
                        <Input type="text" name="occupation" value={this.state.occupation} placeholder="Enter your occupation" onChange={(e) => this.InputHandler(e)} />
                        {this.state.err['occupation_err'] ? <div className="err">{this.state.err['occupation_err']}</div> : ''}
                    </FormGroup>
                    <FormGroup tag="fieldset">
                        <Row>
                            <Col sm="2">
                                <Label className="gender-heading">Gender</Label>
                            </Col>
                            <Col sm="3"><FormGroup check>
                                <Label check className="label-text">
                                    <Input type="radio" name="gender" value='male' onChange={(e) => this.InputHandler(e)} />
                                    Male
                    </Label>
                            </FormGroup>
                            </Col>
                            <Col sm="3">
                                <FormGroup check>
                                    <Label check className="label-text" >
                                        <Input type="radio" name="gender" value='female' onChange={(e) => this.InputHandler(e)} />
                                        Female
                    </Label>
                                </FormGroup>
                            </Col>
                            <Col sm="3">
                                <FormGroup check>
                                    <Label check className="label-text">
                                        <Input type="radio" name="gender" value='others' onChange={(e) => this.InputHandler(e)} />
                                        Others
                    </Label>
                                </FormGroup>
                            </Col>
                        </Row>
                    </FormGroup>
                    <FormGroup tag="fieldset">
                        <Row>
                            <Col sm="2">
                                <Label className="gender-heading">Married</Label>
                            </Col>
                            <Col sm="3"><FormGroup check>
                                <Label check className="label-text">
                                    <Input type="radio" name="marriedstatus" value='Yes' onChange={(e) => this.InputHandler(e)} />
                                    Yes
                    </Label>
                            </FormGroup>
                            </Col>
                            <Col sm="3">
                                <FormGroup check>
                                    <Label check className="label-text" >
                                        <Input type="radio" name="marriedstatus" value='No' onChange={(e) => this.InputHandler(e)} />
                                        No
                    </Label>
                                </FormGroup>
                            </Col>
                        </Row>
                    </FormGroup>
                    <FormGroup>
                        <Row>
                            <Col md={1} className="Age-text" >
                                <FormGroup>
                                    <Label for="exampleDate" className="gender-heading">D.O.B</Label>
                                </FormGroup>
                            </Col>
                            <Col md={11}>
                                <FormGroup>
                                    <Input type="date" name="dob" value={this.state.dob} placeholder="Enter your D.O.B" onChange={(e) => this.InputHandler(e)} />
                                    {this.state.err['dob_err'] ? <div className="err">{this.state.err['dob_err']}</div> : ''}
                                </FormGroup>
                            </Col>
                        </Row>
                    </FormGroup>
                    <Row>
                        <Col md={1} className="Age-text" >
                            <FormGroup>
                                <Label for="age">Age</Label>
                            </FormGroup>
                        </Col>
                        <Col md={11}>
                            <FormGroup>
                                <Input type="number" name="age" value={this.state.age} placeholder="Enter you age" onChange={(e) => this.InputHandler(e)} />
                                {this.state.err['age_err'] ? <div className="err">{this.state.err['age_err']}</div> : ''}
                            </FormGroup>
                        </Col>
                    </Row>
                    <FormGroup>
                        <Label for="familymember" className="label-text">No. of Family member</Label>
                        <Input type="text" name="no_of_family_memeber" value={this.state.no_of_family_memeber} placeholder="Enter your no. of family member" onChange={(e) => this.InputHandler(e)} />
                        {this.state.err['no_of_family_memeber_err'] ? <div className="err">{this.state.err['no_of_family_memeber_err']}</div> : ''}
                        {this.state.isvisible === true ?
                            <div className="form-visible">
                                {this.state.no_of_family_memeber !== 0 ?
                                    <div className="family-info">
                                        {this.family_info.map((item, index) =>
                                            <div key={index} className="inner-form">
                                                <FormGroup row>
                                                    <Label for="family_partner_name" sm={2}>Name</Label>
                                                    <Col sm={10}>
                                                        <Input type="text" maxLength="15" name="family_partner_name" placeholder="Enter name"
                                                            value={this.state['family_partner_name' + index] || ""}
                                                            onChange={(event) => this.setState({ ['family_partner_name' + index]: event.target.value })} />
                                                    </Col>
                                                </FormGroup>
                                                <FormGroup row>
                                                    <Label for="family_partner_occupation" sm={2}>Occupation</Label>
                                                    <Col sm={10}>
                                                        <Input type="text" name="family_partner_occupation" placeholder="Enter occupation"
                                                            value={this.state['family_partner_occupation' + index] || ""}
                                                            onChange={(event) => this.setState({ ['family_partner_occupation' + index]: event.target.value })} />
                                                    </Col>
                                                </FormGroup>
                                                <FormGroup row>
                                                    <Label for="family_partner_relation" sm={2}>Your Relation</Label>
                                                    <Col sm={10}>
                                                        <Input type="text" name="family_partner_relation" placeholder="Enter your relation"
                                                            value={this.state['family_partner_relation' + index] || ""}
                                                            onChange={(event) => this.setState({ ['family_partner_relation' + index]: event.target.value })}
                                                        />
                                                    </Col>
                                                </FormGroup>
                                                <Button color="success" onClick={(e) => this.submitinnerform(e, index)}>Submit First</Button>
                                            </div>
                                        )}
                                    </div>
                                    : ''}
                            </div>
                            : ''}
                    </FormGroup>
                    <FormGroup>
                        <Label for="city" className="label-text">City</Label>
                        <Input type="text" name="city" maxLength="30" value={this.state.city} placeholder="Enter your city" onChange={(e) => this.InputHandler(e)} />
                        {this.state.err['city_err'] ? <div className="err">{this.state.err['city_err']}</div> : ''}
                    </FormGroup>
                    <FormGroup>
                        <Label for="address" className="label-text">Address</Label>
                        <Input type="text" name="address" value={this.state.address} placeholder="Enter your address" onChange={(e) => this.InputHandler(e)} />
                        {this.state.err['address_err'] ? <div className="err">{this.state.err['address_err']}</div> : ''}
                    </FormGroup>
                    <FormGroup>
                        <Label for="area" className="label-text">Area</Label>
                        <Input type="text" name="area" value={this.state.area} placeholder="Enter your area" onChange={(e) => this.InputHandler(e)} />
                        {this.state.err['area_err'] ? <div className="err">{this.state.err['area_err']}</div> : ''}
                    </FormGroup>
                    <FormGroup>
                        <Label for="exampleSelect" className="label-text">State</Label>
                        <select className="selectoption" value={this.state.currentState} onChange={this.handleChange}>
                            {this.statelist.map((item, index) =>
                                <option key={index} ref="option_value" value={item} >{item}</option>
                            )}
                        </select>
                        {this.state.err['state_err'] ? <div className="err">{this.state.err['state_err']}</div> : ''}
                    </FormGroup>
                    <FormGroup>
                        <Label for="contact" className="label-text">Contact</Label>
                        <Input type="text" name="contact" value={this.state.contact} placeholder="Enter your contact" onChange={(e) => this.InputHandler(e)} />
                        {this.state.err['contact_err'] ? <div className="err">{this.state.err['contact_err']}</div> : ''}
                    </FormGroup>
                    <FormGroup>
                        <Label for="email" className="label-text">Email</Label>
                        <Input type="email" name="email" value={this.state.email} placeholder="Enter an email" onChange={(e) => this.InputHandler(e)} />
                        {this.state.err['email_err'] ? <div className="err">{this.state.err['email_err']}</div> : ''}
                    </FormGroup>
                    <FormGroup>
                        <Label for="examplePassword" className="label-text">Password</Label>
                        <Input type="password" name="password" value={this.state.password} placeholder="Enter your password" onChange={(e) => this.InputHandler(e)} />
                        {this.state.err['password_err'] ? <div className="err">{this.state.err['password_err']}</div> : ''}
                    </FormGroup>
                    <Button color="primary" onClick={(e) => this.submitform(e)} >Submit details</Button>
                    <ToastContainer autoClose={5000} />
                </Form>
            </div>
        )
    }
}

export default UserBackgroundForm;
