import React, { Component } from 'react';
import { Jumbotron, Row, Col, Image, Button, FormControl } from 'react-bootstrap';
import logo from '../../assets/imgs/logo.svg';

export class Dashboard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            searchtext: ''
        }
    }

    render() {
        return (
            <div className="dashboard-root">
                <div className="searchabr">
                    <FormControl
                        type="text"
                        className="searchbar-input"
                        value={this.state.searchtext}
                        placeholder="Search..."
                    />
                </div>
                <div className="dashboard">
                    <Row className="section">
                        <Col md={3} className="left-section" >
                            <Image src={logo} rounded />
                        </Col>
                        <Col md={9} className="right-section">
                            <h1 className="align-left">Vivek Rajoriya</h1>
                            <span className="align-left user-info">
                                Occupation: Software Developer
                            </span>
                            <span className="align-left user-info">
                                Email: vivekrajoriya106@gmail.com
                            </span>
                            <span className="align-left user-info">
                                Location: Faridabad
                            </span>
                            <Button className="align-left get-more" bsStyle="primary">Know more</Button>
                        </Col>
                    </Row>
                    <Row className="section">
                        <Col md={3} className="left-section" >
                            <Image src={logo} rounded />
                        </Col>
                        <Col md={9} className="right-section">
                            <h1 className="align-left">Ravi Rajoriya</h1>
                            <span className="align-left user-info">
                                Occupation: Information Security Manger
                            </span>
                            <span className="align-left user-info">
                                Email: ravirajoriya@gmail.com
                            </span>
                            <span className="align-left user-info">
                                Location: Faridabad
                            </span>
                            <Button className="align-left get-more" bsStyle="primary">Know more</Button>
                        </Col>
                    </Row>
                </div>
            </div>
        )
    }
}